import React, { useState } from 'react';
import './Header.css';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import { Button } from '@material-ui/core';
import { IconButton } from '@material-ui/core';
import { useForm } from 'react-hook-form';

import axios from 'axios';
axios.defaults.withCredentials = true;

function Header() {
  const [serverErrors, setServerErrors] = useState('');
  const [userAuth, setUserAuth] = useState(false);
  const [adminAuth, setAdminAuth] = useState(false);
  const [signInMSG, setSignInMSG] = useState('');
  const [hotel, setHotel] = useState([]);
  const navigate = useNavigate();
  const { register, handleSubmit, errors } = useForm();

  window.location.reload();

  const checkAuth = async () => {
    try {
      const response = await axios(
        'https://hotel-booking-webapp.herokuapp.com/auth/check-user',
        {
          headers: { 'content-type': 'application/json' },
          method: 'GET',
        }
      );

      if (response.data.userAuth) {
        setUserAuth(true);
      }

      if (response.data.adminAuth) {
        setAdminAuth(true);
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response.data);
        setServerErrors(error.response.data.message);
        setUserAuth(false);
        setAdminAuth(false);
        alert('Please sign in!');
      } else {
        console.log('Error', error.message);
      }
      console.log(error);
    }
  };

  React.useEffect(() => {
    checkAuth();
    const interval = setInterval(() => {
      checkAuth();
    }, 7200000);
  }, []);

  const logout = async () => {
    try {
      const response = await axios(
        'https://hotel-booking-webapp.herokuapp.com/auth/signout',
        {
          headers: { 'content-type': 'application/json' },
          method: 'GET',
        }
      );

      if (response.data) {
        window.location.reload();
        navigate('/');
      }
    } catch (error) {
      console.log(error);
    }
  };

  const onSubmit = async (data) => {
    let formData = JSON.stringify({
      search: data.search,
    });

    try {
      const response = await axios('http://localhost:5000/search', {
        headers: { 'content-type': 'application/json' },
        method: 'POST',
        data: formData,
      });
      let hotel = response.data.hotel;
      console.log(hotel);
      if (hotel) {
        navigate('/search', { state: hotel });
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response.data);
        setServerErrors(error.response.data.message);
      } else {
        console.log('Error', error.message);
      }
      console.log(error);
    }
  };

  return (
    <div className='header'>
      <div className='header__wrapper'>
        <Link to='/'>
          <img
            className='header__icon'
            src='https://i.pinimg.com/originals/3c/bf/be/3cbfbe148597341fa56f2f87ade90956.png'
            alt='logo'
          />
        </Link>

        <div className='header__center'>
          <form onSubmit={handleSubmit(onSubmit)}>
            <input
              className='search_input'
              type='text'
              name='search'
              placeholder='Enter Location'
              ref={register({ required: true })}
            />
          </form>
        </div>

        <div className='header__right'>
          <Button
            onClick={() => navigate('/search-all')}
            size='small'
            variant='outlined'
          >
            Find All
          </Button>
          {adminAuth ? (
            <Button
              onClick={() => navigate('/admin')}
              size='small'
              variant='outlined'
            >
              Add
            </Button>
          ) : null}
          {userAuth || adminAuth ? (
            <Button onClick={logout} size='small' variant='outlined'>
              Sign out
            </Button>
          ) : null}

          {userAuth || adminAuth ? null : (
            <Button
              onClick={() => navigate('/signin')}
              size='small'
              variant='outlined'
            >
              Sign In
            </Button>
          )}
          <span className='error'>
            {serverErrors ? <p className='tokenExp'>{signInMSG}</p> : null}
          </span>

          <Button
            onClick={() => navigate('/signup')}
            size='small'
            variant='outlined'
          >
            Sign Up
          </Button>
        </div>
      </div>
    </div>
  );
}

export default Header;
