import React, { useState } from 'react';
import './SearchResult.css';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import StarIcon from '@material-ui/icons/Star';
import { useNavigate } from 'react-router-dom';
import { Button } from '@material-ui/core';
import SearchDate from './SearchDate';
import axios from 'axios';
axios.defaults.withCredentials = true;

function SearchResult({ hotel }) {
  const [serverErrors, setServerErrors] = useState('');
  const [showSearch, setShowSearch] = useState(false);
  const [adminAuth, setAdminAuth] = useState(false);
  const [signInMSG, setSignInMSG] = useState('');
  const navigate = useNavigate();

  const checkAuth = async () => {
    try {
      const response = await axios(
        'https://hotel-booking-webapp.herokuapp.com/auth/check-user',
        {
          headers: { 'content-type': 'application/json' },
          method: 'GET',
        }
      );

      if (response.data.adminAuth) {
        setAdminAuth(true);
      }
    } catch (error) {
      if (error.response) {
        console.log(error.response.data);
        setServerErrors(error.response.data.message);
        setAdminAuth(false);
        setSignInMSG('Please sign in!');
      } else {
        console.log('Error', error.message);
      }
      console.log(error);
    }
  };

  const onClick = () => {
    navigate(`/admin/edit/${hotel._id}`);
  };

  React.useEffect(() => {
    checkAuth();
    const interval = setInterval(() => {
      checkAuth();
    }, 7200000);
  }, []);

  return (
    <div className='searchResult'>
      <img src={hotel.imageUrl} alt='' />
      <FavoriteBorderIcon className='searchResult__heart' />

      <div className='searchResult__info'>
        <div className='banner__search'>
          {showSearch && <SearchDate hotelId={hotel._id} />}

          <Button
            onClick={() => setShowSearch(!showSearch)}
            className='banner__searchButton'
            variant='outlined'
          >
            {showSearch ? 'Hide' : 'Search Dates'}
          </Button>
        </div>
        <div className='searchResult__infoTop'>
          <p>Location: {hotel.location}</p>
          <h3>{hotel.title}</h3>
          <p>____</p>
          <p>{hotel.description}</p>
        </div>

        <div className='searchResult__infoBottom'>
          <div className='searchResult__stars'>
            <StarIcon className='searchResult__star' /> 4.5
          </div>
          <div className='searchResults__price'>
            <h2 className='hotel_price'>{hotel.price}</h2>
            {adminAuth ? (
              <Button onClick={onClick} size='small' variant='outlined'>
                Edit
              </Button>
            ) : null}
          </div>
        </div>
      </div>
    </div>
  );
}

export default SearchResult;
